body {
    margin: 0px;
    overflow: hidden;
}

.sky,
.sea,
.seafoam,
.sand,
.sand-front,
.wet-sand,
.palm-tree,
.leaves,
.trunk,
.leaves-1,
.leaves-2,
.leaves-3 {
    position: absolute;
}

.sky {
    height: 40%;
    width: 100%;
    background: linear-gradient(to bottom, #037ccb 0%, #82ccef 100%);
}

.sea {
    height: 30%;
    width: 200%;
    left: -50%;
    top: 40%;
    border-radius: 0 0 50% 50%;
    background: linear-gradient(to bottom,
            rgba(8, 122, 193, 1) 0%,
            rgba(18, 156, 192, 1) 25%,
            rgba(42, 212, 229, 1) 50%,
            rgba(150, 233, 239, 1) 75%,
            rgba(222, 236, 211, 1) 100%);
    animation: wave ease-in-out 10s infinite;
    transform-origin: 50% 0%;
    overflow: hidden;
}

@keyframes wave {
    0% {
        transform: scaleY(1);
    }

    35% {
        transform: scaleY(1.3);
    }

    69% {
        transform: scaleY(1);
    }

    100% {
        transform: scaleY(1);
    }
}

.seafoam {
    height: 100%;
    width: 100%;
    background: linear-gradient(to bottom,
            transparent 0%,
            transparent 90%,
            white 100%);
    animation: seafoam ease-in-out 10s infinite;
}

@keyframes seafoam {
    0% {
        opacity: 0;
    }

    30% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 0;
    }
}

.sand {
    height: 35%;
    width: 100%;
    background: #fdf1d7;
    top: 65%;
}

.sand-front {
    height: 10%;
    width: 100%;
    background: #fdf1d7;
    top: 90%;
}

.wet-sand {
    height: 37.5%;
    width: 200%;
    left: -50%;
    top: 40%;
    border-radius: 0 0 50% 50%;
    background: #ecc075;
    box-shadow: 0 10px 10px 0 #ecc075;
    animation: wetsand ease-in-out 10s infinite;
}

@keyframes wetsand {
    0% {
        opacity: 0.2;
    }

    34% {
        opacity: 0.2;
    }

    35% {
        opacity: 0.7;
    }

    100% {
        opacity: 0.2;
    }
}

.palm-tree,
.leaves {
    height: 100%;
    width: 100%;
}

.trunk {
    height: 200%;
    width: 70%;
    left: 25%;
    top: -10%;
    border-radius: 50%;
    border-bottom: 100px solid transparent;
    border-right: 100px solid #aa8366;
}

.leaves-1 {
    height: 70%;
    width: 50%;
    left: 50%;
    top: -25%;
    transform: rotate(-20deg);
    border-radius: 50%;
    border-top: 200px solid #394d00;
}

.leaves-2 {
    height: 90%;
    width: 50%;
    left: 40%;
    top: -30%;
    transform: rotate(10deg);
    border-radius: 50%;
    border-top: 200px solid #5c7301;
}

.leaves-3 {
    height: 90%;
    width: 50%;
    left: 65%;
    top: -30%;
    transform: rotate(15deg);
    border-radius: 50%;
    border-top: 200px solid #465a05;
}